@import url('./__copyrights/footer__copyrights.css');
@import url('./__made-by/footer__made-by.css');
@import url('./__link/footer__link.css');

.footer {
  display: flex;
  justify-content: space-between;
  width: 100%;
  box-sizing: border-box;
  padding: 20px 15px;
  flex-direction: column;
  align-items: center;

  color: #fff;
  font-family: 'Inter', 'Helvetica', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.8;
}

@media screen and (min-width: 500px) {
  .footer {
    flex-direction: row;
  }
}



@media screen and (min-width: 768px) {
  .footer {
    font-size: 15px;
    line-height: 1.5;
  }

}

@media screen and (min-width: 1280px) {
  .footer {
    font-size: 16px;
    line-height: 1.4;
  }
}
