.main__logo-link {
  height: 50px;
  width: 50px;
  text-decoration: none;
  transition: opacity .1s linear .1s;

  /* additional padding for screen min-width 320px  */
  padding-top: 3px;
}

.main__logo-link:hover {
  opacity: .8;
}

@media screen and (max-width: 1024px) {
  .main__logo-link {
    height: 40px;
    width: 40px;
  }
}

@media screen and (max-width: 320px) {
  .main__logo-link {
    height: 24px;
    width: 24px;
  }
}

@media screen and (min-width: 768px) {
  .main__logo-link {
    padding-top: 5px;
  }
}
